/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>Your dear author recently posted about the ambivalence and misunderstanding that abounds with regard to the concept of RSS. Based on this weeks hilarious (in a laughing-at-you, not-with-you way) <a href=\"http://www.catalystgroupdesign.com/cofactors/?p=111\">usability survey</a> of blogging from Catalyst Group Design, blogging (yes, the entire concept) is about a mainstream as the Kabbalah. </p>\n<p>The people they interviewed (about 25 of them) were smart folks in a wealthy, highly industrialized country. They used the internet all the time. But when they came to a normal blog, they were stumped. They asked: What kind of a website is this? What are these categories? Are they organized chronologically?</p>\n<p>These are not stupid questions — they just sound stupid to folks who work with blog publishing software. </p>\n<p>The point is this, geeks: people don't understand what blogs are. They don't know how to use them effectively and, often, they don't know how to read them. They certainly don't know how to subscribe to them via RSS. </p>\n<p>If you are a blogger, this may be a difficult concept for you to accept. It was for me. </p>\n<p>But ask around. Ask your friends who aren't geeks. Ask the people at work. I'll bet you a dollar they don't know what a blog is. I'll bet five they don't read them, and I'll bet you ten they would be intimidated as hell by the idea of creating their own. (And if I lose that, I'll bet $50 that, once they start it, they won't keep up with it.)</p>\n<p>For now, we (bloggers) need to recognize that our readership is other bloggers. Don't get depressed:  that's a pretty decent concept, mate. We can have our little blogger community and create positive change among ourselves. But clearly, we'll have to move on to reach other users ... like real people. </p>\n<p>So this technical-sounding \"usability study\" of blogs means a great deal for people trying to create change with online publishing. Here are a few tips that I have tried to follow here: </p>\n<p>For one: If you are a blogger, design down your site. I think that simplicity has an inherent currency in all forms of learning — and all publications are deigned to teach. Consider that your blog, as a text-based medium, would do better to take influences from the world of books (remember those?) than cable TV. </p>\n<p>For two: Write for real people, not just geeks. Surely, there is a place for geeky forums, but if your online work is meant to be relevant for people with real lives (present readership excluded), try to avoid terms like trackback and ping, which have a clinically-proven tendency to cause impotence and sleep apnea.</p>","frontmatter":{"created_at":"August 05, 2005","path":"/2005/08/05/this-island-blog-or-what-the-hell-is-a-blog","title":"This Island Blog. (Or, What the Hell is a Blog?)","tags":["blogging","literacy"]}}},"pathContext":{}}

/***/ })

